namespace $ { export class $mol_app_hello extends $mol_viewer {

	/// namerHint \Name
	namerHint() {
		return "Name"
	}

	/// name \
	@ $mol_mem()
	name( next? : any ) {
		return ( next !== void 0 ) ? next : ""
	}

	/// namer $mol_stringer 
	/// 	hint < namerHint 
	/// 	value > name
	@ $mol_mem()
	namer( next? : any ) {
		return new $mol_stringer().setup( obj => { 
			obj.hint = () => this.namerHint()
			obj.value = ( next? : any ) => this.name( next )
		} )
	}

	/// greeting \
	greeting() {
		return ""
	}

	/// greeter $mol_viewer childs / < greeting
	@ $mol_mem()
	greeter( next? : any ) {
		return new $mol_viewer().setup( obj => { 
			obj.childs = () => [].concat( this.greeting() )
		} )
	}

	/// childs / 
	/// 	< namer 
	/// 	< greeter
	childs() {
		return [].concat( this.namer() , this.greeter() )
	}

} }

